import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router";
import VueResource from 'vue-resource'
 
//debug
Vue.config.debug = true;
 
Vue.use(VueRouter);
Vue.use(VueResource);
 
// , 
const First = { template: '<div><h2> 1 </h2></div>' }
import secondcomponent from './component/secondpage/secondcomponent.vue'
import firstcomponent from './component/firstpage/firstcomponent.vue'
const first = { template: '<div><h2> 1</h2></div>' }
import pageone from './component/firstpage/pageone.vue'
import pagetwo from './component/firstpage/pagetwo.vue'
import './static/js/jquery.min.js'

// 
// 
const router = new VueRouter({
  //mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      component: First
    },
    {
      path: '/first',
      component: firstcomponent,
      children: [
        //  route record
        {
	      path: '/first/',
	      name:'first',
	      component: first
	    },
        {
	      path: '/first/firstp',
	      name:'first',
	      component: first
	    },
	    {
	      path: '/first/pageone/:userId',
	      name:'pageone',
	      component: pageone
	    },
	    {
	      path: '/first/pagetwo/:userId',
	      name:'pagetwo',
	      component: pagetwo
	    }
      ]
    },
    {
      path: '/second',
      component: secondcomponent
    }
  ]
})
 
// 
//  App  #app 
const app = new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')


