<template>
	<div id="firstcomponent">
		<img src="../../static/assets/logo.png"/>
	    <h1>{{ msg }}</h1>
	    <span v-bind:title="message">
	    	
	    </span>
	    <div v-if='seen'>
	    	
	    </div>
	    <div>
	    	<ul>
	    		<li v-for='it in todos'>{{ it.text }}</li>
	    	</ul>
	    </div>
	    <div>
			<p>{{ message }}</p>
			<button v-on:click="reverseMessage"></button>
		</div>
		<input v-model="message" v-on:change="changeVal">
		<ul>
		    <li v-for="article in articles">
		        {{article.title}}
		    </li>
	    </ul>	
      	<ul>
        	<li><router-link :to="{ name: 'first', params: { userId: 123 }}">/</router-link></li>
        	<li><router-link :to="{ name: 'pageone', params: { userId: 123 }}">/123</router-link></li>
        	<li><router-link :to="{ name: 'pagetwo', params: { userId: 123 }}">/223</router-link></li>
      	</ul>
      	<router-view class="view"></router-view>
	</div>
</template>

<script>
	export default{
	    data () {
		    return {
		      msg: 'Welcome to Your Vue.js App',
		      message: ' ' + new Date(),
		      seen:false,
		      todos: [
			      { text: ' JavaScript' },
			      { text: ' Vue' },
			      { text: '' }
			    ],
			    groceryList: [
			      { text: '' },
			      { text: '' },
			      { text: '' }
			    ]
		    }
	    },
	    mounted: function() {
		    this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=10', {}, {
		        headers: {
		
		        },
		        emulateJSON: true
		    }).then(function(response) {
		      // 
		
		        this.articles = response.data.subjects
		        // this.articles = response.data["subjects"] 
		
		    }, function(response) {
		        // 
		        console.log(response)
		    });
	    },
		methods: {
		    reverseMessage: function () {
		      	this.message = this.message.split('').reverse().join('')
		    },
		    changeVal: function(){
		    	console.log(this);
		    	alert(this.message);
		    }
		}
	}
</script>

<style>
</style>


